<script lang="js">

  export default {
    methods: {
      openCVOptions() {
        this.hideSections();
        this.$refs.cvOptions.style.display = "flex";

        setTimeout(() => {
          this.$refs.cvOptions.style.display = "none";
          this.showSections();
        }, 20000)
      },
      hideCVOptions() {
        this.$refs.cvOptions.style.display = "none";
        this.showSections();
      },
      hideSections() {
        this.$refs.brand.style.opacity = "0";
        this.$refs.about.style.opacity = "0";
        this.$refs.more.style.opacity = "0";
      },
      showSections() {
        this.$refs.brand.style.opacity = "1";
        this.$refs.about.style.opacity = "1";
        this.$refs.more.style.opacity = "1";
      }
    }
  }

</script>

<style lang="scss" scoped>

