import Vue from "vue";
import VueRouter from "vue-router";

import Home from "../views/Home.vue";
import About from "../views/About.vue";
import notFound from "../views/notFound.vue";

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/about",
    name: "About",
    component: About
  },
  {
    path: "*",
    name: "notFound",
    component: notFound
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

function getRandom(min, max) {
  return Math.floor(Math.random() * (max - min) + min);
}

let x = [180, 270, 360, 450, 540, 630, 720]; //define your options
let ang;
// eslint-disable-next-line no-unused-vars
router.beforeEach((to, from, next) => {

  let rotate = document.getElementById("gBackground");

  let oAng = ang;
  ang = getRandom(0, 5); //pick one option

  while(ang == oAng) { //your option can't be the one you picked before
    ang = getRandom(0, 5);
  }

  if(rotate != null) {
    if(to.name == "Home") {
      rotate.style.transform = `rotate(1080deg)`
    } else rotate.style.transform = `rotate(${x[ang]}deg)`
  }
// ================================================================================
  setTimeout(() => { window.scrollTo({ top: 0, behavior: 'smooth'}) }, 600);
// ================================================================================
  next()
}) 

export default router;
